.profileBack {
    margin: -25px -25px 0px -25px;
}
.profileBack img {
    width: 100%;
    border-radius: 15px 15px 0px 0px;
}
.avatar {
    text-align: center;
}
.avatar img {
    padding: 10px;
    background-color: #fff;
    width: 200px;
    border-radius: 50%;
    margin-top: -100px;
}
.discription {
    text-align: center;
}
.social a img {
    width: 25px;
    margin: 10px;
}
.discription button {
    float: none;
}
.edit {
    position: absolute;
    top: 25em;
    left: 48em;
    opacity: 0;
    z-index: 999;
}
.edit img {
    width: 50px;
    background: #fff;
    border-radius: 50px;
    cursor: pointer;
}
.discription:hover .edit {
    opacity: 100;
}
form {
    text-align: left;
}
form button {
    margin-top: 20px;
}
.error {
    color: red;
}