
.post {
    display: grid;
    grid-template-areas:
    "a t l";
    padding: 20px 0px;
    grid-template-columns: 1fr 10fr 1fr;
}
.postAvatar {
    grid-area: a;
}
.postAvatar img {
    width: 80px;
    border-radius: 50%;
    padding: 10px;
    background: #fff;
}
.postText {
    grid-area: t;
}
.postText p {
    margin: 0px 30px;
    padding-top: 15px;
}
.postLikes {
    grid-area: l;
    text-align: center;
}
.postLikes div{
    padding-top: 6px;
}
.postLikes img {
    width: 25px;
    margin-top: 40px;
}
.post:hover .deletePost{
    opacity: 100;
}
.deletePost {
    opacity: 0;
    background: none;
    color: #000;
    float: right;
    border: none;
    border-radius: 0px;
    padding: 0!important;
}